import axios, { AxiosRequestConfig } from "axios";
import { message } from "antd";
import React from "react";

const BASE_URL = "/api";

export interface RestResponse<T> {
  code: number;
  msg: string;
  data: T;
}

// axios
const ajax = axios.create({
  baseURL: BASE_URL,
  timeout: 10000, //  10s
});

// request
ajax.interceptors.request.use(
  (config) => {
    config.headers["Accept"] = "*/*";
    config.headers["X-Requested-With"] = "XMLHttpRequest";
    return config;
  },
  (error) => {
    message.error("");
    Promise.reject(error);
  }
);

// response 
ajax.interceptors.response.use(
  (response) => {
    return response;
  },
  (error) => {
    if (error?.response) {
      const value = error.response.data;
      if (typeof value === "string" || value instanceof String) {
        //.
        message.error(<span>. {error?.response.data}</span>);
      } else {
        //json.
        message.error(
          <span>
            . {value?.msg} {JSON.stringify(value?.data)}
          </span>
        );
      }
      return Promise.reject(error);
    }
    message.error("");
    return Promise.reject(error);
  }
);

//
export type ListDataReq<T> = {
  filter: T;
  //. name
  fuzzyMatch: boolean;
  pageNo: number;
  pageSize: number;
};

//
export type PageResult<T> = {
  total: number;
  records: T[];
};

export function getAction<T>(url: string, params?: any) {
  return ajax.get<RestResponse<T>>(url, { params } as AxiosRequestConfig);
}

export function postAction<T>(url: string, param: any) {
  return ajax.post<RestResponse<T>>(url, param);
}

export function putAction<T>(url: string, param: any) {
  return ajax.put<RestResponse<T>>(url, param);
}

export function deleteAction<T>(url: string, param: any) {
  return ajax.delete<RestResponse<T>>(url, param);
}

//----------api-------
export type LoadAvgDto = {
  load1m: number;
  load5m: number;
  load10m: number;
};
export const getLoadAvg = () => getAction<LoadAvgDto>("/loadavg");
